import Vue from 'vue'
import App from './App'
import routers from './router/index'
import VueRouter from 'vue-router'
import 'lib-flexible/flexible.js'
Vue.use(VueRouter);
Vue.config.productionTip = false;
const router = new VueRouter({
    // mode : 'history',
    routes : routers
});
var userAStr = navigator.userAgent;
router.beforeEach(({ path}, from, next) => {
    if (path=='/agreement' && userAStr.indexOf('rrkd')!='-1') {
        return next({path: '/agreementRrkd'})
    }
    next()
                    
});
new Vue({
    el: '#apps',
    router :router ,
    render: h=> h(App)
});



