import Home from '../pages/Home.vue'
const routers = [
    {
        path: '/',
        name: 'homeApp',
        component : Home
    },
    {
        path: '/agreement',
        name: 'agreement',
        component : () => import('../pages/agreePage/agreement')
    },
    {
        path: '/agreementRrkd',
        name: 'agreementRrkd',
        component : () => import('../pages/agreePage/agreementRrkd')
    }
]
export default  routers



